@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
@import url(https://use.fontawesome.com/releases/v5.14.0/css/all.css);
html,
body {
  height: 100%;
  width: 100%;
  background-color: #f3f2ef;
  margin: 0;
  padding: 0;
  left: 0;
  top: 0;
  font-size: 100%;
  font-family: 'Open Sans', sans-serif;
  overflow: hidden;
}

input,
strong,
p {
  margin: 0;
  padding: 0;
  border: 0;
}

video::-webkit-media-controls {
  display: none !important;
}

@media screen and (max-height: 630px) {
  html,
  body {
    font-size: 80%;
  }
}

.hidden {
  display: none !important;
}

.width0 {
  width: 0% !important;
}
/* 1 */
.width-peer0 {
  width: 50% !important;
  margin-left: auto;
  margin-right: auto;
}

/* 2 */
.width-peer1 {
  width:50% !important;

}
/* 3 */
.width-peer2{
  width:33.33% !important;
}

/* 4 */
.width-peer3{
  width:25% !important;
}
/* 5 */
.width-peer4{
  width:20% !important;
}
/* 6 */
.width-peer5,.width-peer6,.width-peer7,.width-peer8 {
  width:20% !important;
}


/* mobile-L-425px--view */
@media (min-width:250px) and (max-width:425px){
  .width-peer0{
    width: 100% !important;
  }
  .width-peer1 ,.width-peer2,.width-peer3,.width-peer4,.width-peer5,.width-peer6,.width-peer7,.width-peer8 {
    width:50% !important;

  }
}


/* tab-768px--view */
@media (min-width:426px) and (max-width:768px){
  .width-peer0{
    width: 100% !important;
  }
  .width-peer1 ,.width-peer2,.width-peer3,.width-peer4,.width-peer5,.width-peer6,.width-peer7,.width-peer8 {
    width:50% !important;

  }
}
