#roomName{
  margin-right:12px;
  display:none;
}

.category_active{
  border:1px solid #a833b9;
  background: #a833b9;
  width: 80px;
  border: none;
  border-radius: 15px;
  padding: 5px;
  margin: 4px;
font-size: 14px;
color:#fff;
font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
height:auto;
cursor: pointer;
}
.category_active:hover{
  color:#a833b9;
  background-color: #fff;
  border:1px solid #a833b9;
}
.category{
  color: #a833b9;
  background-color: #37103d;
  border:1px solid #a833b9;
  width: 80px;
  border: none;
  border-radius: 15px;
  padding: 5px;
  margin: 4px;
font-size: 14px;
font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
height:auto;
cursor: pointer;
 
}
.category:hover{
  color:#fff;
  background-color:#a833b9;
  border:1px solid #fff;
}

.list{
  display:flex;
  margin-left:10px;
}
/* overlay */


.booth-hover-box {
  position: absolute;
  left: 0px;
  z-index: 9;
  top: 0%;
  background: rgba(20, 20, 43, 0.7);
  border: 2px solid #a833b9;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0px auto;
  text-align: center;
  display: none;
}
.booth-btns a {
  display: block;
  margin-bottom: 16px;
}
.booth-btns .chat-btn-light {
  padding: 5px 33px;
}
.booth-hover-box .sort-icon {
  left: 8px;
  top: 18px;
}
.booth-hover-box .club-m-name {
  font-size: 18px;
}
.booth-hover-box .cross-icon {
  line-height: 20px;
}
.chat-box:hover .booth-hover-box {
  display: flex;
}
.line-left-sctn ul li:hover .lounge-hover-box {
  display: block;
}

@media (min-width:250px) and (max-width:425px){
  .list{
    display:block;
    margin-left:-50px;

  }
  .h1_tag{
    font-size:31px;
  }
 
}
.h1_tag{
letter-spacing: -1.2px;
}
.chat-user-fig{
  display:flex;

}
.name_msg{
  display:block;
  margin-left:10px;
}

.name_msg > strong {
  margin-left: 3px;
  color:#fff;
  font-size:15px;
}

.name_msg > p {
  max-width: 65%;
  width: auto;
  padding: 4px;
  color: #fff;
  font-size: 13px;
}

